/* Mobile ------------------------ */

@media screen and (max-width: 990px) {
  /* Global */
  h2 {
    font-size: 1.3rem;
  }
  /* Mobile Carets 🔼🔽 */
  .summary h2 > i:nth-child(1),
  .my-tasks h2 > i:nth-child(1),
  .my-queue h2 > i:nth-child(1),
  .week-stats h2 > i:nth-child(1) {
    display: inline-block;
    cursor: pointer;
    margin-left: 10px;
    color: var(--darkBlue);
    font-size: 2rem;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }

  /* Mobile Expansion / Collapse */
  .summary > h2 + div,
  .my-tasks > h2 + div,
  .my-queue > h2 + div,
  .week-stats > h2 + div {
    opacity: 1;
    -webkit-transition: all 1s;
    transition: all 1s;
  }

  .my-queue > h2 + div.not-visible,
  .my-tasks > h2 + div.not-visible,
  .summary > h2 + div.not-visible,
  .week-stats > h2 + div.not-visible {
    opacity: 0;
    display: none;
  }

  /* DashboardView -> WeeklyPerformance */
  .bar-graph-group-item > img {
    height: 65px;
  }
  .bar-graph-group-item .progress {
    top: -48px;
    left: 61px;
    height: 30px;
    width: 85%;
  }

  .bar-graph-group-item .progress-bar {
    font-size: 1.2rem;
    font-weight: 400;
    padding-left: 5px;
  }

  /* DashboardView -> MyTasks */
  .my-tasks .all-tasks button {
    width: 75%;
    height: 90%;
    font-size: 1rem;
    opacity: 0.65;
  }
  /* DashboardView -> TeamSummary */
  .circ1,
  .circ2,
  .circ3 {
    margin-left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  .summary h3 {
    font-size: 1.3rem;
    font-weight: 300;
  }
  .summary p {
    font-size: 1.1rem;
    font-weight: 300;
  }
  .summary div.c100 {
    margin: 0px 0px 15px 0px;
    margin-left: 50%;
  }

  .summary div.col-4 {
    border-bottom: 1px solid var(--subtleGrey);
    padding-bottom: 25px;
    margin-bottom: 15px;
  }
  .summary div.col-4:last-of-type {
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 0;
  }
  .c100.big {
    font-size: 200px;
  }
  .c100 span > img {
    width: 135px;
  }
  .c100 span > img:hover {
    width: 145px;
  }

  /* DashboardView -> TeamSummary -> CaseButton */
  .btn-case .fa-trash,
  .btn-case .fa-pen {
    font-size: 1.3rem;
    right: 0.7rem;
    top: 0.7rem;
  }

  .my-queue h3 {
    margin-top: 10px;
    margin-bottom: 8px;
  }
}

